




















import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import { load } from '@/sdk';
import { AppLoader } from './components/common';

@Component({
  components: {
    AppLoader,
  },
})
export default class App extends Vue {
  loading = true;

  async created(): Promise<void> {
    await load();
    this.loading = false;
  }
}
